.root {
  background-color: #FD5C63; }

.title {
  margin: 0 0 1.5rem 0; }

.card {
  display: flex;
  margin: 0 0 3rem 0;
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 6px; }

.text {
  padding: 1.5rem 1.5rem 1.5rem 1.5rem;
  flex: 1.2; }

.profileContainer {
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 9999px;
  margin-right: 12px;
  min-width: 40px; }

.profileImage {
  width: 40px;
  height: 40px;
  min-width: 40px; }

.reporter {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 0 0 1.5rem 0; }

.heading {
  margin: 0 0 1.5rem 0; }

.name {
  margin: 0 0 0.5rem 0; }

.role {
  margin: 0;
  color: rgba(0, 0, 0, 0.54); }

.map {
  flex: 1;
  background: #FCE7DD;
  height: 320px; }
