.root {
  height: 100%; }

.normal {
  width: 100%;
  margin-right: auto;
  margin-left: auto; }
  @media (max-width: 599px) {
    .normal {
      max-width: 100%;
      padding-right: 24px;
      padding-left: 24px; } }
  @media (min-width: 600px) {
    .normal {
      max-width: 100%;
      padding-right: 24px !important;
      padding-left: 24px !important; } }
  @media (min-width: 900px) {
    .normal {
      max-width: 100%; } }
  @media (min-width: 1224px) {
    .normal {
      max-width: 100%;
      padding-right: 80px !important;
      padding-left: 80px !important; } }
  @media (min-width: 1800px) {
    .normal {
      max-width: 100%;
      padding-right: 144px !important;
      padding-left: 144px !important; } }

.narrow {
  width: 100%;
  margin-right: auto;
  margin-left: auto; }
  @media (max-width: 599px) {
    .narrow {
      max-width: 100%;
      padding-right: 24px;
      padding-left: 24px; } }
  @media (min-width: 600px) {
    .narrow {
      max-width: 100%;
      padding-right: 24px !important;
      padding-left: 24px !important; } }
  @media (min-width: 900px) {
    .narrow {
      max-width: 100%; } }
  @media (min-width: 1224px) {
    .narrow {
      max-width: 1080px; } }

.bleed {
  width: 100%;
  margin-right: auto;
  margin-left: auto; }

.fixLeft {
  margin-left: 15vw;
  padding-left: 1.5rem; }

@media (max-width: 599px) {
  .fixRight {
    margin-right: 0; } }

@media (min-width: 600px) {
  .fixRight {
    margin-right: 0; } }

@media (min-width: 900px) {
  .fixRight {
    margin-right: 25vw; } }
