.root {
  font-family: "Masbul", "geomanist", "SF Pro Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; }

.display1 {
  font-size: 128px;
  line-height: 1;
  letter-spacing: -1.44px;
  font-weight: 800; }

.display2 {
  font-size: 96px;
  line-height: 1;
  letter-spacing: -1.628px;
  font-weight: 800; }

.display3 {
  font-size: 64px;
  line-height: 1;
  font-weight: 700;
  letter-spacing: -1.44px;
  font-weight: 800; }

.heading1 {
  composes: root;
