  letter-spacing: -0.8px;
  font-weight: 700; }

.heading4 {
  composes: root;
  color: rgba(0, 0, 0, 0.84);
  font-size: 18px;
  line-height: 26px;
  letter-spacing: -0.8px;
  font-weight: 700; }

.heading4Alt {
  composes: root;
  color: rgba(0, 0, 0, 0.84);
