.large {
  composes: root;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400; }

.medium {
  composes: root;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400; }

.small {
  composes: root;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400; }

.captionBold {
  composes: root;
  display: inline-block;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 16px; }

.caption {
  composes: root;
  display: inline-block;
  font-size: 12px;
  font-weight: 400;
