.message {
  font-family: "Masbul", "geomanist", "SF Pro Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.54); }

.raw {
  padding-bottom: 0; }

.link {
  cursor: pointer;
  font-family: "Masbul", "geomanist", "SF Pro Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  color: #008380;
  text-decoration: none;
  transition: all 0.32s; }
  .link:hover {
    transition: all 0.28s;
    text-decoration: underline; }

.left {
  text-align: left; }

.center {
  text-align: center; }

.right {
  text-align: right; }

.breakWord {
  hyphens: auto;
  word-wrap: break-word; }

.truncate {
  display: block;
  max-width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: normal; }

.chevron {
  margin-left: 0.5rem;
  animation: chevronTransform 0.4s 1 cubic-bezier(0.8, 0, 0.2, 1); }

@keyframes chevronTransform {
  0% {
    margin-left: 0; }
  100% {
    margin-left: 4px; } }

.fancyLink {
  font-family: "Masbul", "geomanist", "SF Pro Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  width: fit-content;
  color: #008380;
  text-decoration: none;
  background-image: linear-gradient(180deg, #008380 30%, #008380 30%);
  background-size: 0% 1.5px;
  background-repeat: no-repeat;
  background-position: 0 100%;
  transition: all 0.12s;
  cursor: pointer; }
  .fancyLink:hover {
    background-size: 100% 1.5px;
    text-shadow: 2px 0 white, -2px 0 white, 0 2px white, 0 -2px white, 2px 0 white, -2px 0 white, 2px 0 white, -2px 0 white, 2px 0 white, -2px 0 white, 2px 0 white, -2px 0 white;
    text-decoration: underline;
    color: #00514F; }
