.root {
  background: #008380;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  padding: 0.75rem 0 0.75rem 0;
  composes: container-bleed from global;
  z-index: 99999; }

.container {
  composes: container-bleed from global;
  justify-content: space-between;
  align-items: center;
  display: flex; }

.brand {
  display: inline-flex;
  align-items: center;
  min-height: 36px;
  justify-content: flex-start;
  margin: 0 1.5rem 0 1.5rem; }

.logo {
  max-height: 20px;
  margin: 0 0.75rem 0 0; }

.name {
  color: white !important; }

.github {
  margin: 0 1.5rem 0 1.5rem; }

.githubLink {
  height: fit-content;
  display: inherit; }
